
.userlogo {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: #ccc;
  position: fixed;
}
@media screen and (max-width: 768px) {
.userlogo {
    margin-left: 34%;
    margin-top: 20%;
}
}
@media screen and (min-width: 600px) and (max-width: 960px) {
.userlogo {
    margin-left: 42%;
    margin-top: 13%;
}
}
@media screen and (min-width: 960px) {
.userlogo {
    margin-left: 46%;
    margin-top: 5%;
}
}



